import * as Tone from 'tone';
import instrumentsConfig from './instruments';

class AudioEngine {
  constructor() {
    this.isPlaying = false;
    this.bpm = 120;
    this.transport = Tone.Transport;
    this.transport.bpm.value = this.bpm;
    this.transport.timeSignature = [4, 4];
    this.sequences = [];
    this.currentBlockIndex = 0;
    this.totalBlocks = 0;
    this.instruments = {};

    // Добавляем переменные для метронома и свинга
    this.metronomeEnabled = false;
    this.swingValue = 0; // от 0 до 50
    this.metronome = null;
    this.metronomeVolume = new Tone.Volume(-10); // Регулируемая громкость

    // Инициализируем инструменты
    this.initializeInstruments();
    this.initializeMetronome();
  }

  // Получение текущего time signature
  getTimeSignature() {
    // Если есть настроенные блоки, используем их количество для time signature
    if (this.totalBlocks > 0) {
      return [this.totalBlocks, 4];
    }

    // Если нет блоков, возвращаем текущий transport time signature
    const ts = this.transport.timeSignature;

    if (ts === null || ts === undefined) {
      return [4, 4];
    }

    if (Array.isArray(ts)) {
      return ts;
    }

    if (typeof ts === 'number') {
      return [ts, 4];
    }

    return [4, 4];
  }

  // Инициализация метронома
initializeMetronome() {
  // Создаем простой синтезатор для метронома
  this.metronome = new Tone.Synth({
    oscillator: { type: 'triangle' },
    envelope: { attack: 0.001, decay: 0.1, sustain: 0, release: 0.1 }
  }).toDestination();

  // По умолчанию беззвучный (технически "выключен")
  this.metronome.volume.value = -Infinity;
  this.normalMetronomeVolume = -10; // Запоминаем нормальную громкость

  // Метроном начально выключен
  this.metronomeEnabled = false;
  this.metronomeEvent = null;
}

// Включение/отключение метронома (просто переключаем громкость)
toggleMetronome() {
  this.metronomeEnabled = !this.metronomeEnabled;

  // Меняем только громкость в зависимости от состояния
  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  } else {
    this.metronome.volume.value = -Infinity; // Полностью беззвучный
  }

  // Если воспроизведение идет, но метроном еще не запущен - запускаем
  if (this.isPlaying && this.metronomeEvent === null) {
    this.startMetronome();
  }

  return this.metronomeEnabled;
}

// Инициализация метронома
initializeMetronome() {
  // Создаем простой синтезатор для метронома
  this.metronome = new Tone.Synth({
    oscillator: { type: 'triangle' },
    envelope: { attack: 0.001, decay: 0.1, sustain: 0, release: 0.1 }
  }).toDestination();

  this.normalMetronomeVolume = -10;
  this.metronome.volume.value = -Infinity; // По умолчанию выключен
  this.metronomeEnabled = false;
  this.metronomeLoop = null;
}

// Включение/отключение метронома
toggleMetronome() {
  this.metronomeEnabled = !this.metronomeEnabled;

  // Меняем только громкость
  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  } else {
    this.metronome.volume.value = -Infinity;
  }

  return this.metronomeEnabled;
}

// Запуск метронома - используем Tone.Loop вместо scheduleRepeat
startMetronome() {
  // Удаляем старый loop, если существует
  this.stopMetronome();

  // Получаем размер такта
  const timeSignature = this.getTimeSignature();
  const beatsPerBar = Array.isArray(timeSignature) ? timeSignature[0] : 4;

  // Создаем петлю, которая будет запускаться каждую четверть
  this.metronomeLoop = new Tone.Loop((time) => {
    // Получаем текущую позицию из транспорта
    const position = Tone.Transport.position;
    const splitPos = position.split(':');

    // Берем только текущую долю из позиции (формат: "мера:доля:подразделение")
    // splitPos[0] = такт, splitPos[1] = доля в такте
    const beat = parseInt(splitPos[1]); 

    // Первая доля такта - сильная, остальные - слабые
    if (beat === 0) {
      this.metronome.triggerAttackRelease('C5', '16n', time);
    } else {
      this.metronome.triggerAttackRelease('G4', '16n', time);
    }
  }, '4n').start(0);
}

// Остановка метронома
stopMetronome() {
  if (this.metronomeLoop) {
    this.metronomeLoop.stop();
    this.metronomeLoop.dispose();
    this.metronomeLoop = null;
  }
}

// Регулировка громкости метронома
setMetronomeVolume(volume) {
  this.normalMetronomeVolume = Math.max(-40, Math.min(0, volume));

  if (this.metronomeEnabled) {
    this.metronome.volume.value = this.normalMetronomeVolume;
  }

  return this.normalMetronomeVolume;
}

  // Установка значения Swing
  setSwing(swingValue) {
    // Ограничиваем значение от 0 до 50
    this.swingValue = Math.max(0, Math.min(50, swingValue));
    this.updateSwing();
    return this.swingValue;
  }

  // Обновление настроек Swing
  updateSwing() {
    // Преобразуем свинг в формат, который понимает Tone.js (от 0 до 1)
    const swingAmount = this.swingValue / 100 * 2; // 50 превращается в 1
    Tone.Transport.swingSubdivision = '8n'; // Восьмые ноты для свинга
    Tone.Transport.swing = swingAmount;

    // Если есть метрономный паттерн, также обновляем его
    if (this.metronomePattern) {
      this.metronomePattern.playbackRate = 1;
    }
  }

  // Обновляем метод setBPM
  setBPM(bpm) {
    this.bpm = bpm;
    this.transport.bpm.value = bpm;
    // Если активен метроном, обновляем его также
    this.updateSwing();
  }

  // Инициализация инструментов
  async initializeInstruments() {
    const loadPromises = Object.entries(instrumentsConfig).map(([instrumentId, config]) => {
      return new Promise(resolve => {
        const urls = {};
        for (const note in config.urls) {
          urls[note] = config.urls[note];
        }

        const sampler = new Tone.Sampler({
          urls: urls,
          baseUrl: config.baseUrl,
          onload: () => {
            if (typeof config.volume === 'number') {
              sampler.volume.value = config.volume;
            }

            this.instruments[instrumentId] = sampler;
            resolve();
          }
        }).toDestination();
      });
    });

    // Ждем загрузки всех инструментов
    await Promise.all(loadPromises);
  }

  // Получение инструмента
  getInstrument(instrumentId) {
    return this.instruments[instrumentId] || new Tone.Synth().toDestination();
  }

  // Подготовка данных секвенсора к воспроизведению
  prepareSequencerData(sequencerStructure, cellsState) {
    this.sequences = [];
    this.totalBlocks = sequencerStructure.length;

    // Обновляем time signature на основе количества блоков
    this.transport.timeSignature = [this.totalBlocks, 4];
    console.log(`Установлен размер ${this.totalBlocks}/4`); // Для отладки

    // Очищаем все существующие события
    this.transport.cancel();

    sequencerStructure.forEach((block, blockIndex) => {
      const trackSchedule = {};

      if (block && block.trackSteps) {
        Object.entries(block.trackSteps).forEach(([trackName, stepCount]) => {
          trackSchedule[trackName] = [];

          // Для каждого шага в треке
          for (let step = 0; step < stepCount; step++) {
            const cellId = `${blockIndex}-${trackName}-${step}`;
            const cell = cellsState[cellId];

            if (cell && cell.instrumentId) {
              // Рассчитываем время воспроизведения в долях блока
              const time = step / stepCount;

              trackSchedule[trackName].push({
                time,
                note: cell.note || 'C4',
                instrumentId: cell.instrumentId,
                duration: 1 / stepCount
              });
            }
          }
        });
      }

      this.sequences.push(trackSchedule);
    });

    // Настраиваем события
    this.scheduleBlocks();

    // Если метроном активен, обновляем его с новым time signature
    if (this.metronomeEnabled) {
      this.stopMetronome(); // Сначала остановим
      this.startMetronome(); // Затем запустим с новым размером
    }

    return true;
  }

  // Планирование воспроизведения блоков
  scheduleBlocks() {
    const blockDuration = '4n';
    let startTime = 0;

    this.sequences.forEach((blockTracks, blockIndex) => {
      Object.entries(blockTracks).forEach(([trackName, events]) => {
        events.forEach(event => {
          // Рассчитываем абсолютное время
          const absoluteTime = startTime + (event.time * Tone.Time(blockDuration).toSeconds());

          // Планируем событие в транспорте
          this.transport.schedule((time) => {
            const instrument = this.getInstrument(event.instrumentId);
            instrument.triggerAttackRelease(
              event.note,
              event.duration * Tone.Time(blockDuration).toSeconds(),
              time
            );
          }, absoluteTime);
        });
      });

      // Увеличиваем время для следующего блока
      startTime += Tone.Time(blockDuration).toSeconds();
    });

    // Устанавливаем цикл
    this.transport.loop = true;
    this.transport.loopEnd = startTime;
    this.transport.loopStart = 0;

    return true;
  }

  getPosition() {
    return this.transport.seconds;
  }

// Обновляем методы воспроизведения
async play() {
  try {
    await Tone.start();

    // Если ставили на паузу и продолжаем - НЕ запускаем метроном заново
    const wasPlaying = Tone.Transport.state === "paused";

    this.transport.start();
    this.isPlaying = true;

    // Запускаем метроном только если это не продолжение после паузы
    // или если метроном еще не запущен
    if (!wasPlaying || !this.metronomeLoop) {
      this.startMetronome();
    }

    // Визуальный планировщик
    const visualScheduler = await import('./VisualScheduler').then(module => module.default);
    visualScheduler.setAudioEngine(this);
    visualScheduler.start();

    return true;
  } catch (error) {
    console.error("Ошибка при запуске аудио:", error);
    return false;
  }
}

pause() {
  if (this.isPlaying) {
    this.transport.pause();
    this.isPlaying = false;

    // НЕ останавливаем метроном при паузе - он остановится сам с транспортом

    // Визуальный планировщик
    const visualScheduler = require('./VisualScheduler').default;
    visualScheduler.pause();
  }
  return true;
}

stop() {
  this.transport.stop();
  this.isPlaying = false;

  // При полной остановке ВСЕГДА останавливаем метроном
  this.stopMetronome();

  // Визуальный планировщик
  const visualScheduler = require('./VisualScheduler').default;
  visualScheduler.stop();

  return true;
}

  // Переключение воспроизведения
  async togglePlay() {
    try {
      // Явно стартуем аудиоконтекст для соблюдения политики автовоспроизведения
      if (Tone.context.state !== 'running') {
        await Tone.start();
      }

      if (this.isPlaying) {
        this.pause();
      } else {
        await this.play();
      }
      return this.isPlaying;
    } catch (error) {
      console.error("Ошибка при переключении воспроизведения:", error);
      return false;
    }
  }
}

// Создаем синглтон аудио движка
const audioEngine = new AudioEngine();
export default audioEngine;